body{
  padding: 0;
  margin:0;
  font-family: Emoona;
}

@font-face{
  font-family: Emoona;
  src:url('font/Avenir-Light.ttf');
}

@font-face{
  font-family: EmoonaM;
  src:url('font/Avenir-Medium.ttf');
}

@font-face{
  font-family: EmoonaH;
  src:url('font/Avenir-Heavy.ttf');
}

hr{
  background-color: #d6d6d6;
  color: #d6d6d6;
  border:0 none;
  height: 3px;
}

.col-md-4{
  padding: 0;
}




/*--------------------NAVBAR--------------------------*/
.navbar{
  padding:0;
  margin:0;
  position: fixed;
  top:0;
  z-index: 10;
  width: 100%;

}
.navbar a{
  text-decoration: none;
  color: white;
}

.navbar1{
  background-color: black;
  height: auto;
  width: 100%;

  flex-direction: column;
  display: flex;
  justify-content: flex-end;
  text-align: center;
}
.navbar1  li{
  list-style: none;
  display: inline-block;
  color: white;
  font-size: 14px;
  float:right;
  padding-right: 2.8rem;
  text-align: center;
  letter-spacing: 1px;

}
.navbar1 ul{
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.navbar1 span{
  font-weight: 600;
}

.navbar2{
  background-color: rgba(0,0,0,0.53);
  height: 8vh;
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar2 li{

  list-style: none;
  display: inline-block;
  color: white;
  padding:0 1.5rem 0 1.5rem;
  text-align: center;

}
.navbar2 ul{
  font-size: 14px;
  margin:0;
  padding: 0;
}

.title{
  letter-spacing: 5px;
  font-size: 18px;

}
.navbar-search input[type=text]{
  background-color: #404040;
  background-image: url('../img/searchicon.png');
  background-size: 22px;
  background-repeat: no-repeat;

  border-radius: 3px;
  outline: none;
  color: white;
  border: 0;
  width: 15px;
  height: 3vh;
  padding-left: 22px;


  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;

}
.navbar-search input[type=text]:focus{
  width: 150px;
}

.navbar-search :hover{
  cursor: default;
}



