/*---------------------------------------------------*/
@media screen and (max-width: 900px){

  .footer li{
    font-size: 14px;
    padding:0 1.5rem 0 1.5rem;
  }
  .content-box:after{
    padding-bottom: 20%;
  }
  .text-content-box{
    padding-top: 5vh;

  }
  .line-content-box-1{
    font-size: 20px;
  }
  .line-content-box-2{
    font-size: 20px;
  }
  .line-content-box-3{
    font-size: 14px;
  }

  .box-featured{
    width: 150px;
    height: 225px;
  }

  .wrapper-home-content-2{
    font-size: 12px;
  }

  .subsc-content-wrapper{
    padding: 4% 15% 4% 15%;

  }

  .cart-container{
    height: 100vh;
    width: 100vw;
    overflow: hidden;
  }

  .navbar-dropdown-account .form-group input{
    width: 55vw;
  }

}

.center-box{
  width:100%;
  height:100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.center-box .modal-dialog{
  min-width: 45%;
}

.hidden {
  transform: translateY(-100px);
  opacity: 0;
  z-index: -100;
  margin-bottom: -50px;
}
.notification {
  transition: all .3s ease-in-out;
}

@media screen and (min-width : 901px) and (max-width : 1024px){

  .box-featured{
    width: 200px;
    height: 300px;
  }

  .subsc-content-wrapper{
    padding: 5% 20% 5% 20%;

  }
}
