/* Jeffry's Stuff */
.dropdown-align-right {
  right: 0;
  left: auto;
}

.navbar-dropdown{
  padding: 0;
  border-radius: 0;
  color: black;
  background-color: #2d2d2d;
}

.navbar-dropdown-account{
  padding: 15px;
  color: white;
}

.navbar-dropdown-account .form-group input{
  width: 20vw;
}

.form-group-password{
  margin-bottom: 3px;
}

.btn-login{
  background-color: #5c5c5c;
  border: none;
  color: white;
  padding: 5px 15px 5px 15px;
  text-align: center;
  display: inline-block;
  float: right;
  cursor: pointer;
}

.btn-login:focus, .btn-login:active{
  outline: none;
}

.navbar-dropdown-input { 
  border: 0;
  padding: 5px;
}

.navbar-dropdown-input:focus{
  outline:none;
}

/*--------------------------Bag--------------------------*/

.item-image{
  width: 80px;
  height: 80px;
  background-size: cover;
  background-position: center top;
}
.item-description{
  float:left;
  color: white;

  h4 
  {
    font-size: 18px;
  }

  h5 
  {
    font-size: 16px;
  }

  span 
  {
    font-size: 14px;
  }
}
.item-edit a{
  color: white !important;
}

.item-edit a:hover{
  color:grey !important;
}

.cart-container{
  height: 50vh;
  width: 30vw;
  overflow: hidden;
}

.cart-item-container{
  width: 100%;
  height:80%;
  overflow-y:auto;

  .cart-empty
  {
    width:100%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;

    h4 
    {
        color:white;
    }
  }

}

.cart-item-container hr{
  margin-left:15px;
  margin-right: 15px;
}


.cart-item{
  /*background-color: green;*/
  margin: 15px;
}

.cart-confirmation{
  margin:15px;
  color: white;
  font-family: inherit;

  h5 
  {
    font-size: 16px;
  }
}

.btn-checkout{
  background-color: rgb(92, 92, 92);
  font-family: inherit;
  border: none;
  color: white;
  padding: 10px 15px 10px 15px;
  text-align: center;
  display: inline-block;
  float: right;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.btn-checkout:hover{
  background: rgba(92, 92, 92, 0.5);
}

.btn-checkout:focus, .btn-checkout:active{
  outline: 0;
}

.link-register{
  color: white;
  text-decoration-color: white;
  display: inline-block;
  float: left;
  margin-top: 8px;
}

.link-forgot{
  font-size: 12px;
  font-family: inherit;
  margin-bottom: 15px;
  display: inline-block;
  width: 100%;
}

.cart-total{
  color: white;
}

.dropdown-menu
{
  background-color: rgb(25,25,25);

  .dropdown-item
  {
    transition: all .3s ease-in-out;
  }
}